import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { Avatar, Popover, Icon, message } from 'antd';

import './index.scss';

class HeaderWrapper extends Component {
  render() {
    return (
      <div className='header-wrapper'>
        <Link to="/">
          <h2 className="logo fl">后台管理</h2>
        </Link>
        <div className="user-info fr">
          <Avatar shape="square" size={40} icon="user" />
          <Popover content={<p style={{fontSize: 12}} className="cursor" onClick={logout}><Icon type="logout" />退出</p>}>
            <div className="user fr cursor">
              <p>mingxingshuo</p>
              <p>运营部<Icon type="down" /></p>
            </div>
          </Popover>
        </div>
      </div>
    )
  }


}

const logout = () => {
  localStorage.setItem("userInfo", "");
  message.success("退出登录成功");
  setTimeout(() => {
    window.location.href = "/admin/login?redirect_url=" + encodeURIComponent(window.location.pathname);
  }, 500)
};

export default HeaderWrapper;
