import React, { Component } from 'react';
import BreadCrumb from '../../../common/bread';
import { Button, Table, Modal, Radio, message, Input } from 'antd';
import { connect } from "react-redux";
import { actionCreators } from './store';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Link } from 'react-router-dom';


class NovelLink extends Component {
  componentWillMount() {
    this.props.getNovelLinkList();
    this.props.getChannelList()

  }

  render() {
    const { novelLinkList, count, domain, domainModal, channelModal, channel, channelList, selectedChannel, changePage, deleteNovel, copyNovel, selectChannel, cancelSelectChannel, openChannelModal, changeChannel, openDomainModal, closeDomainModal, submitDomain, changeDomainName } = this.props;
    const novelLinkHeader = [
      {
        key: "id",
        dataIndex: "id",
        title: "id",
        align: "center"
      },
      {
        title: "类型",
        align: "center",
        render: (text, record) => record.type === 0 ? "微跳" : record.type === 1 ? "非微调单页" : record.type === 2 ? "非微调多页" : "头条推广"
      },
      {
        key: "name",
        dataIndex: "name",
        title: "公号",
        align: "center"
      },
      {
        key: "domain_name",
        dataIndex: "domain_name",
        title: "域名",
        align: "center",
        width: 180,
        render: text => <p style={{width: 180, wordWrap: "break-word"}}>{text}</p>
      },
      {
        key: "channel",
        dataIndex: "channel",
        title: "渠道",
        align: "center",
        render: text => text || "~~"
      },
      {
        key: "remarks",
        dataIndex: "remarks",
        title: "备注",
        align: "center",
        render: text => text || "~~"
      },
      {
        title: "复制链接",
        align: "center",
        render: (text, record) => <CopyToClipboard text={record.domain_name + "/tuiguang/" + (record.type === 0 ? "weitiao/" : record.type === 1 ? "singlepage/" : record.type === 2 ? "multipage/" : "toutiao/") + record.id} onCopy={() => message.success("复制成功")}>
          <Button size="small" style={{background: "lightblue", color: "#fff"}}>复制链接</Button>
        </CopyToClipboard>
      },
      {
        title: "Action",
        align: "center",
        width: 200,
        render: (text, record, index) => <div>
          <Button style={{marginRight: 5}} size="small" type="primary">
            <Link to={{pathname: "./update", query: record}}>修改</Link>
          </Button>
          <Button style={{marginRight: 5}} size="small" type="danger" onClick={() => deleteNovel(record.id, index)}>删除</Button>
          <Button size="small" onClick={() => copyNovel(record, this.props)}>复制</Button>
        </div>
      }
    ];

    const pagination = {
      pageSize: 20,
      total: count,
      onChange: page => changePage(page),
    };
    const ChannelListWrapper = channelList.map(item => <Radio.Button style={{marginRight: 10, marginBottom: 10}} value={item.name} key={item._id}>{item.name}</Radio.Button>);
    return (
    	<div>
        <BreadCrumb />
        <div className="container">
          <Button className="fr" style={{marginLeft: 20}} onClick={openDomainModal}>修改域名</Button>

          <Button className="fr" type="primary">
            <Link to="./create">新建小说链接</Link>
          </Button>
          <Button style={{marginBottom: 20}} onClick={openChannelModal}>{selectedChannel}</Button>
          <Table bordered size="small" rowKey={record => record._id} columns={novelLinkHeader} dataSource={novelLinkList} pagination={{...pagination}} />
        </div>
        <Modal
          title="请选择渠道"
          visible={channelModal}
          onOk={() => selectChannel(channel)}
          onCancel={cancelSelectChannel}
        >
          <Radio.Group value={channel} onChange={changeChannel}>
            <Radio.Button style={{marginRight: 10}} value="默认">默认</Radio.Button>
            {ChannelListWrapper}
          </Radio.Group>
        </Modal>
        <Modal
          title="请输入新域名"
          visible={domainModal}
          onOk={() => submitDomain(domain)}
          onCancel={closeDomainModal}
        >
          <Input placeholder="请输入新域名，如：https://www.baidu.com" value={domain} onChange={changeDomainName} />
        </Modal>
    	</div>
    )
  }

}

const mapState = state => ({
  novelLinkList: state.novel.links.novelLinkList,
  count: state.novel.links.count,
  channelModal: state.novel.links.channelModal,
  channel: state.novel.links.channel,
  channelList: state.novel.links.channelList,
  selectedChannel: state.novel.links.selectedChannel,
  domainModal: state.novel.links.domainModal,
  domain: state.novel.links.domain
});

const mapDispatch = dispatch => ({
  getNovelLinkList() {
    dispatch(actionCreators.getNovelLinkList())
  },
  changePage(page) {
    dispatch(actionCreators.changePage(page))
  },
  deleteNovel(id, index) {
    dispatch(actionCreators.deleteNovel(id, index))
  },
  copyNovel(record, props) {
    record.id += '_copy';
    dispatch(actionCreators.submitNovel(record, props));
  },
  getChannelList() {
    dispatch(actionCreators.getChannelList())
  },
  openChannelModal() {
    dispatch(actionCreators.openChannelModal())
  },
  cancelSelectChannel() {
    dispatch(actionCreators.cancelSelectChannel())
  },
  selectChannel(channel) {
    dispatch(actionCreators.selectChannel(channel))
  },
  changeChannel(e) {
    dispatch(actionCreators.changeChannel(e.target.value))
  },
  openDomainModal() {
    dispatch(actionCreators.openDomainModal())
  },
  closeDomainModal() {
    dispatch(actionCreators.closeDomainModal())
  },
  submitDomain(domain) {
    dispatch(actionCreators.submitDomain(domain))
  },
  changeDomainName(e) {
    dispatch(actionCreators.changeDomainName(e.target.value))
  }
});

export default connect(mapState, mapDispatch)(NovelLink);